<template lang="html">
  <div class="data-table">
    <div class="data-table-item head">
      <div class="data-table-item__left tac fl"></div>
      <div class="data-table-item__center tac fl"></div>
      <div class="data-table-item__right tac fl"></div>
    </div>
    <div class="data-table-item" v-for="item in tableData">
      <div class="data-table-item__left tac fl">{{item.name}}</div>
      <div class="data-table-item__center tac fl">{{item.callBy || 0}}</div>
      <div class="data-table-item__right tac fl">{{item.sort || 0}}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    tableData: {
      type: Array,
      default () {
        return []
      }
    }
  }
}
</script>

<style lang="less">
@import '../../assets/css/variable.less';
@import '../../assets/css/property.less';

.data-table {
  .data-table-item {
    width: 100%;
    height: .88rem;
    font-size: .32rem;
    background: @whiteColor;
    border-bottom: .01rem solid @lightColor;
    &.head {
      color: @whiteColor;
      background: @activeColor;
      .data-table-item__left {
        background-size: .44rem .44rem;
      }
      .data-table-item__center {
        text-indent: -.1rem;
        background-size: .3rem .5rem;
      }
    }
    > div {
      overflow: hidden;
      padding: 0 .22rem;
      width: 33.33%;
      line-height: .88rem;
      border-right: .02rem solid @tablerightColor;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    > div:last-child {
      border-right: none;
    }
  }
  & > div:nth-child(2n+1) {
    color: @whiteColor;
    background: @tablebgColor;
  }
}
</style>



