<template lang="html">
  <div class="keywords-page">
    <default-head header=""></default-head>
    <div class="keywords">
      <data-table :tableData="keyWordData"></data-table>
    </div>
  </div>
</template>

<script>
import api from '@/api'
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import dataTable from '@/components/common/dataTable'

export default {
  components: { defaultHead, dataTable },
  data () {
    return {
      keyWordData: []
    }
  },
  computed: {
    userId () {
      return utils.userId()
    }
  },
  created () {
    this.initPage()
  },
  methods: {
    isLogin () {
      if (!utils.isLogin()) {
        this.$router.push('/login')
        return false
      }
      return true
    },
    initPage () {
      if (!this.isLogin()) return
      this.ajaxGetEnterpriseTagsById({userId: this.userId})
    },
    ajaxGetEnterpriseTagsById (data) {
      minutils.IndicatorOpen('...')
      api.getEnterpriseTagsById(data).then(response => {
        minutils.IndicatorClose()
        let result = response.body
        if (result.isSuccess && result.data) {
          this.keyWordData = result.data
        }
      })
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';

.keywords-page {
  padding-top: .88rem;

}
</style>



